import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import moment from 'moment';
import 'whatwg-fetch';
import Row from './Row';
import base from './base';

//https://bl.ocks.org/mbostock/4573883
//https://github.com/jonnyhaynes/inline-svg

class App extends Component {
  constructor() {
    super();
    this.state = {
      // currentMoment
      currentMoment: moment().utcOffset('-0600'),
      moonPhase: {},
      weather: {},
      riders: {},
      movies: {},
      sun: [],
      gas: {
        prices: [],
        avgPrice: 0
      },
    }
    ;
  }
  componentDidMount() {
    this.timer = setInterval(() => {
      this.setState({currentMoment: moment().utcOffset('-0600')})
    }, 1000);
  }

  componentWillMount() {
    // this runs right before the <App> is rendered
    this.ref1 = base.bindToState(`moonPhase`, {
      context: this,
      state: 'moonPhase'
    });
    this.ref2 = base.bindToState(`weather`, {
      context: this,
      state: 'weather'
    });
    this.ref3 = base.bindToState(`riders`, {
      context: this,
      state: 'riders'
    });
    this.ref4 = base.bindToState(`movies`, {
      context: this,
      state: 'movies'
    });
    this.ref5 = base.bindToState(`gas`, {
      context: this,
      state: 'gas'
    });
    this.ref5 = base.bindToState(`sun`, {
      context: this,
      state: 'sun'
    });
    // // check if there is any order in localStorage
    // const localStorageRef = localStorage.getItem(`order-${this.props.params.storeId}`);
    //
    // if(localStorageRef) {
    //   // update our App component's order state
    //   this.setState({
    //     order: JSON.parse(localStorageRef)
    //   });
    // }

  }

  componentWillUnmount() {
    base.removeBinding(this.ref1);
    base.removeBinding(this.ref2);
    base.removeBinding(this.ref3);
    base.removeBinding(this.ref4);
    base.removeBinding(this.ref5);
    base.removeBinding(this.ref6);

    clearTimeout(this.timer);
  }

  // componentDidMount() {
  //   fetch('https://raw.githubusercontent.com/dubbs/city-data/master/city.json?' + Date.now(), {}).then(function(response) {
  //     return response.text();
  //   }).then(function(text) {
  //     return JSON.parse(text);
  //   }).then(this.mySetState);
  // }
  // mySetState(json) {
  //   this.setState(json);
  // }
  render() {
    var weatherKey = Object.keys(this.state.weather)[0];
    var weather = this.state.weather[weatherKey] || {};
    var moonPhase = this.state.moonPhase;

    // Sun Stuff
    var momentNow = this.state.currentMoment;
    var currentSunPhase = this.state.sun.reduce(function (acc, x, i) {
      var itemPhase = moment.parseZone(x.endDate);
      if (acc.obj.title) {
        return acc;
      }
      if (momentNow.isBefore(itemPhase)) {
        return {
          index: i,
          obj: x
        };
      }
      return acc;
    }, {
      index: 0,
      obj: {}
    });
    var currentSunPhaseObject = currentSunPhase.obj;
    var currentSunPhaseIndex = currentSunPhase.index;
    var nextSunPhaseIndex = currentSunPhaseIndex === this.state.sun.length - 1
      ? 1
      : currentSunPhaseIndex + 1
    ;
    var nextSunPhaseText = this.state.sun.length ? this.state.sun[nextSunPhaseIndex].title : '';
    var momentNext = moment.parseZone(currentSunPhaseObject.endDate);
    var momentDiff = momentNow.to(momentNext, true);
    var momentNextTime = momentNext.format('h:mm a');

    //
    return (
      <div className="app">
        <Row title="Weather" value1={weather.condition} value2={weather.temperature} icon={weather.imgSrc} sourceText="Environment Canada" sourceHref="https://weather.gc.ca/city/pages/sk-40_metric_e.html" weatherItems={this.state.weather} />
        <Row title="Sun" value1={`${nextSunPhaseText} ${momentDiff}`} value2={currentSunPhaseObject.title} icon="./icons/other/svg/004-sunset.svg" sun={this.state.sun} sunCurrentPhaseIndex={currentSunPhaseIndex} sourceText="Time and Date - Astronomy" sourceHref="https://www.timeanddate.com/astronomy/canada/saskatoon" />
        <Row title="Moon Phase" value1={moonPhase.percentage} value2={moonPhase.phaseText} icon={moonPhase.imgSrc} sourceText="Time and Date" sourceHref="https://www.timeanddate.com/moon/phases/canada/saskatoon" />
        {/*<Row title="Riders" value1="Oct 13 8:00 pm" value2="vs. Redblacks" icon="./icons/riders.svg" riders={this.state.riders} sourceText="TSN" sourceHref="http://www.tsn.ca/cfl/team/saskatchewan-roughriders/schedule" />*/}
        {/*<Row title="Gas" value1="Average Price" value2={`${this.state.gas.avgPrice}¢`} icon="./icons/other/svg/001-dispenser-2.svg" gasPrices={this.state.gas.prices}  />*/}
        {/*<Row title="Movies" value1="" value2="Cineplex" icon="" movies={this.state.movies} sourceText="Cinema Clock" sourceHref="https://www.cinemaclock.com/theatres/scotiabank-saskatoon-vip" />*/}



        {/*https://weather.gc.ca/forecast/public_bulletins_e.html?Bulletin=fpcn48.cwao*/}
        {/*<Row title="UV Index" value1="&nbsp;" value2="Low" icon="/icons/uv-ray-warning.svg" />*/}
        {/*https://weather.gc.ca/airquality/pages/next18Hourlies/skaq-002_e.html*/}
        {/*https://weather.gc.ca/airquality/pages/skaq-002_e.html*/}
        {/*<Row title="Air Quality Index" value1="Low Risk" />*/}
        {/*https://www.timeanddate.com/moon/phases/canada/saskatoon*/}

        {/**/}
        {/*<Row title="Sunrise/Sunset" value1="7:09 am - 6:40 pm" />*/}
        {/**/}
        {/*<Row title="Moonrise/Moonset" value1="5:54 pm - 4:21 am" />*/}
        {/*https://www.saskatchewan.ca/business/employment-standards/vacations-holidays-leaves-and-absences/public-statutory-holidays/list-of-saskatchewan-public-holidays*/}
        {/*<Row title="Next Holiday" value1="October 9" value2="Thanksgiving" icon="/icons/holidays/svg/009-turkey.svg" showBody="true" sourceText="The Canadian Encyclopedia" sourceHref="http://www.thecanadianencyclopedia.ca/en/article/thanksgiving-day/"/>*/}
        {/*https://weather.gc.ca/warnings/index_e.html?prov=sk*/}
        {/*<Row title="Warnings" value="" />*/}
        {/*https://weather.gc.ca/warnings/index_e.html?prov=sk*/}
        {/*<Row title="Observances" value1="" />*/}
      </div>
    );
  }
}

export default App;



